} */

.strokesize {
  padding: 0rem;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 3rem;
  color: black;
  font-weight: bold;
  font-family: YurukaStd;
}

.normal {
  min-width: 300px; 
  align-items: center;
  justify-content: center;
  color: black;
  font-weight: bold;
  font-family: YurukaStd;
}
.normal .MuiFormControl-root {
  min-width: 150px; 
  flex-grow: 1; 
  flex-basis: 0; 
}

.linesetting {
  
  padding: 0rem;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 3rem;
  color: black;
  font-weight: bold;
  font-family: YurukaStd;

}

