::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 1rem;
  min-height: 2rem;
}

.counter {
  left: 0;
  top: 0;
  text-align: left;
  color: black;
  font-weight: bold;
  font-family: YurukaStd;
}

.color-pickers-container {
  padding: 1rem;
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 2rem;
  color: black;
  font-weight: bold;
  font-family: YurukaStd;
  
}

.img-loader-container {
  align-items: center;
  justify-content: center;
}